.toolbar {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  min-height: 40px;
  margin-bottom: 50px;

  &__input-file {
    display: none;
  }

  &__label {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    cursor: pointer;
  }
}
